(function ($) {
  var $window = $(window),
    $body = $("body"),
    $wrapper = $("#wrapper"),
    $header = $("#header"),
    $banner = $("#banner"),
    $modal = $("#myModal"),
    $playBtn = $("#play-btn"),
    $pauseBtn = $("#pause-btn"),
    $audio = $("#modalAudio")[0],
    $beforeText = $("#before-text");

  // var textToType = `
  //   Hello HR or whomever it may concern.|
  //   I'm out of station for a week 23/02/2025 to 03/03/2025.|
  //   Please leave a message on given whatsapp or e-mail.|
  //   I will get back to you on the same.|
  //   Thanking you!|
  //   Enjoy music.
  // `;
  var textToType = `
    Hello Fellas,|
    Facing n/w issues (call drops, no connectivity) due to n/w upgradation in our area from last 4 Days.|
    For that I have upgrade my SIM card, factory resetted my phone to zero configuration.|
    Still facing the same.|
    So, it's a humble request to text, whatsapp or email given. I'll get back to you ASAP.|
    Enjoy music.
  `;
  var currentIndex = 0;
  var typingSpeed = 50; // Speed at which text appears (in ms)

  // Function to type text character by character
  function typeText() {
    if (currentIndex < textToType.length) {
      if (textToType.charAt(currentIndex) === "|") {
        $beforeText.before(`<br />`);
      } else {
        $beforeText.before(`<span>${textToType.charAt(currentIndex)}</span>`);
      }
      currentIndex++;
      setTimeout(typeText, typingSpeed); // Continue typing
    } else {
      $playBtn.show();
      $playBtn.click(() => {
        $playBtn.hide();
        $pauseBtn.show();
        $audio.play();
      });

      $pauseBtn.click(() => {
        $pauseBtn.hide();
        $playBtn.show();
        $audio.pause();
      });
    }
  }

  // Breakpoints.
  breakpoints({
    xlarge: ["1281px", "1680px"],
    large: ["981px", "1280px"],
    medium: ["737px", "980px"],
    small: ["481px", "736px"],
    xsmall: ["361px", "480px"],
    xxsmall: [null, "360px"],
  });

  /**
   * Applies parallax scrolling to an element's background image.
   * @return {jQuery} jQuery object.
   */
  $.fn._parallax =
    browser.name == "ie" || browser.name == "edge" || browser.mobile
      ? function () {
          return $(this);
        }
      : function (intensity) {
          var $window = $(window),
            $this = $(this);

          if (this.length == 0 || intensity === 0) return $this;

          if (this.length > 1) {
            for (var i = 0; i < this.length; i++)
              $(this[i])._parallax(intensity);

            return $this;
          }

          if (!intensity) intensity = 0.25;

          $this.each(function () {
            var $t = $(this),
              on,
              off;

            on = function () {
              $t.css(
                "background-position",
                "center 100%, center 100%, center 0px"
              );

              $window.on("scroll._parallax", function () {
                var pos =
                  parseInt($window.scrollTop()) - parseInt($t.position().top);

                $t.css(
                  "background-position",
                  "center " + pos * (-1 * intensity) + "px"
                );
              });
            };

            off = function () {
              $t.css("background-position", "");

              $window.off("scroll._parallax");
            };

            breakpoints.on("<=medium", off);
            breakpoints.on(">medium", on);
          });

          $window
            .off("load._parallax resize._parallax")
            .on("load._parallax resize._parallax", function () {
              $window.trigger("scroll");
            });

          return $(this);
        };

  // Play initial animations on page load.
  $window.on("load", function () {
    window.setTimeout(function () {
      $body.removeClass("is-preload");
      $modal.fadeIn();
      typeText();
    }, 100);
  });

  // Clear transitioning state on unload/hide.
  $window.on("unload pagehide", function () {
    window.setTimeout(function () {
      $(".is-transitioning").removeClass("is-transitioning");
    }, 250);
  });

  $(".close").click(function () {
    $modal.fadeOut(); // Fade-out effect for closing the modal
    $audio.pause(); // Stop the audio when modal is closed
    $audio.currentTime = 0;
  });

  // Fix: Enable IE-only tweaks.
  if (browser.name == "ie" || browser.name == "edge") $body.addClass("is-ie");

  // Scrolly.
  $(".scrolly").scrolly({
    offset: function () {
      return $header.height() - 2;
    },
  });

  // Tiles.
  var $tiles = $(".tiles > article");

  $tiles.each(function () {
    var $this = $(this),
      $image = $this.find(".image"),
      $img = $image.find("img"),
      $link = $this.find(".link"),
      x;

    // Image.

    // Set image.
    $this.css("background-image", "url(" + $img.attr("src") + ")");

    // Set position.
    if ((x = $img.data("position"))) $image.css("background-position", x);

    // Hide original.
    $image.hide();

    // Link.
    if ($link.length > 0) {
      $x = $link.clone().text("").addClass("primary").appendTo($this);

      $link = $link.add($x);

      $link.on("click", function (event) {
        var href = $link.attr("href");

        // Prevent default.
        event.stopPropagation();
        event.preventDefault();

        // Target blank?
        if ($link.attr("target") == "_blank") {
          // Open in new tab.
          window.open(href);
        }

        // Otherwise ...
        else {
          // Start transitioning.
          $this.addClass("is-transitioning");
          $wrapper.addClass("is-transitioning");

          // Redirect.
          window.setTimeout(function () {
            location.href = href;
          }, 500);
        }
      });
    }
  });

  // Header.
  if ($banner.length > 0 && $header.hasClass("alt")) {
    $window.on("resize", function () {
      $window.trigger("scroll");
    });

    $window.on("load", function () {
      $banner.scrollex({
        bottom: $header.height() + 10,
        terminate: function () {
          $header.removeClass("alt");
        },
        enter: function () {
          $header.addClass("alt");
        },
        leave: function () {
          $header.removeClass("alt");
          $header.addClass("reveal");
        },
      });

      window.setTimeout(function () {
        $window.triggerHandler("scroll");
      }, 100);
    });
  }

  const $altText = $("#header .logo .change-text"),
    field = [
      "Profession",
      "Passion",
      "Enthusisam",
      "Love",
      "Devotion",
      "Dedication",
      "Interest",
      "Obsession",
      "Expertiseness",
    ];

  // Function to get a random color
  function getRandomColor() {
    let letters = "0123456789ABCDEF",
      color = "#";
    for (let i = 0; i < 6; i++) {
      color += letters[Math.floor(Math.random() * 16)];
    }
    return color;
  }

  let inc = 1;
  // Function to switch the text
  function changeText() {
    const newText = field[inc++];
    if (inc === 9) {
      inc = 0;
    }

    // Fade out the current text
    $altText.fadeOut(1000, function () {
      // Using jQuery fadeOut with a callback
      // After the fade out animation, change the text content
      $altText.text(newText); // Change the text content

      // Set a random color for the new text
      var randomColor = getRandomColor();
      $altText.css("color", randomColor);

      // Fade in the new text
      $altText.fadeIn(1000); // Using jQuery fadeIn to smoothly display the new text
    });
  }

  // Change text every 2 seconds
  setInterval(changeText, 2000);

  // Banner.
  $banner.each(function () {
    var $this = $(this),
      $image = $this.find(".image"),
      $img = $image.find("img");

    // Parallax.
    $this._parallax(0.275);

    // Image.
    if ($image.length > 0) {
      // Set image.
      $this.css("background-image", "url(" + $img.attr("src") + ")");

      // Hide original.
      $image.hide();
    }
  });

  // Menu.
  var $menu = $("#menu"),
    $menuInner;

  $menu.wrapInner('<div class="inner"></div>');
  $menuInner = $menu.children(".inner");
  $menu._locked = false;

  $menu._lock = function () {
    if ($menu._locked) return false;

    $menu._locked = true;

    window.setTimeout(function () {
      $menu._locked = false;
    }, 350);

    return true;
  };

  $menu._show = function () {
    if ($menu._lock()) $body.addClass("is-menu-visible");
  };

  $menu._hide = function () {
    if ($menu._lock()) $body.removeClass("is-menu-visible");
  };

  $menu._toggle = function () {
    if ($menu._lock()) $body.toggleClass("is-menu-visible");
  };

  $menuInner
    .on("click", function (event) {
      event.stopPropagation();
    })
    .on("click", "a", function (event) {
      var href = $(this).attr("href");

      event.preventDefault();
      event.stopPropagation();

      // Hide.
      $menu._hide();

      // Redirect.
      window.setTimeout(function () {
        window.location.href = href;
      }, 250);
    });

  $menu
    .appendTo($body)
    .on("click", function (event) {
      event.stopPropagation();
      event.preventDefault();

      $body.removeClass("is-menu-visible");
    })
    .append('<a class="close" href="#menu">Close</a>');

  $body
    .on("click", 'a[href="#menu"]', function (event) {
      event.stopPropagation();
      event.preventDefault();

      // Toggle.
      $menu._toggle();
    })
    .on("click", function (event) {
      // Hide.
      $menu._hide();
    })
    .on("keydown", function (event) {
      // Hide on escape.
      if (event.keyCode == 27) $menu._hide();
    });
})(jQuery);
